@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;800&display=swap);

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}
/* ============ RESET ============ */
a{
    text-decoration: none;
    color: inherit;
}
ul{
    list-style-type: none;
}
/* ============ COLORS ============ */
:root{
    --main-balck:#333333;
    --secondary-black: #393939;
    --grey: #ECECEC;

}
/* ============ GENERAL ============ */
body{
    position: relative;
}
.main-container{
    width: 90vw;
    max-width: 1800px;
    margin: auto;
    position: relative;
}

/* =============================================================================== */
/* =============================================================================== */
/* ######### start: header ######### */
header{
    padding: 1.5rem 0;
}
nav{
    display:flex ;
    justify-content: space-between;
    font-weight:300;
}
.nav__info{
    display: flex;
    justify-content: space-between;
    grid-gap: 9rem;
    gap: 9rem;
}
.nav__list{
    display: flex;
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

@media (max-width:545px) {
    .nav__list{
        display: none;
    }
}

.nav__link{
    -webkit-transition: 0.3s ease;
    transition: 0.3s ease;
}
.nav__link:hover{
    color: grey;
}
.nav__cart span{
    margin-left: 0.4rem;
}
.nav__cart{
    position: relative;
}
.orders{
    background-color: red;
    display: inline-block;
    width: 0.9rem;
    height: 0.9rem;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0.7rem;
    position: absolute;
    left: 0;
    top: 55%;
    border-radius: 50%;
    color: white;
    font-weight: 600;
}

/* ######### start: hero ######### */
.hero{
    margin-top: 30px;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    grid-gap: 30px;
    gap: 30px;

}
.hero__description{
    flex: 1 1;

}

.hero__img{
    flex-basis: 500px;
}
.hero__img img{
    width: 100%;
}

.hero__title{
    font-size: 2.9rem; 
    font-weight: 600;
    padding-right: 2rem;
    line-height: 1.3;
    margin-bottom: 20px;
}
.hero__text{
    
    font-weight: 300;
    padding-right: 10rem;
    margin-bottom: 3.5rem;
    
}
@media (max-width:550px) {
    .hero__text{
        padding-right: 0rem;  
    }
}
/* ----- Button ------- */
.btn{
    border: solid 1px #333333;
    border: solid 1px var(--main-balck);
    padding: 1rem 2rem;
    text-transform: uppercase;
    -webkit-transition: 0.3s ease;
    transition: 0.3s ease;
    cursor: pointer;
}
.btn:hover{
    background-color: #333333;
    background-color: var(--main-balck);
    color: white;
}
.btn-primary{
    background-color: #333333;
    background-color: var(--main-balck);
    color: #fff;
}
.btn-primary:hover{
    background-color: transparent;
    color: #333333;
    color: var(--main-balck);
}

@media (max-width:1224px) {
    .hero__img{
        flex-basis: 400px;
    }
}
@media (max-width:900px) {
    .hero{
        flex-direction: column;
    }
    .hero__img{
        width: 80%;
    }
}
/* ######### start: products ######### */
.products{
    margin: 5rem 0;
    text-align: center;
}

.products h2{
    font-size: 40px;
}
.products__grid{
    margin-top: 30px;
    display: grid;
    grid-template-columns: repeat(auto-fill,minmax(250px,1fr));
    grid-gap: 80px;
    gap: 80px;
}

.product__img img{
    width: 100%;
    -webkit-transition: 0.2s ease;
    transition: 0.2s ease;
}

@media (max-width:645px) {
    .product{
        width: 60%;
        margin-right: auto;
        margin-left: auto;
    }
}
.product__img img:hover{
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}
.grid-title{
    text-align: center;
    font-weight: 400;
    margin-top: 2.5rem;
}
.product__data{
    padding: 0.5rem 0;
    display: flex;
    justify-content: space-between;
    font-weight: 300;
}
.products .btn{
    display: inline-block;
    margin-top: 5rem;
}

/* ####### start: newsletter ####### */
.newsletter{
    display: flex;
    flex-wrap: wrap;
    background-color: #ECECEC;
    width: 100%;
    justify-content: space-around;
    padding: 0 0 20px;
    margin-bottom: 30px;

}
.newsletter__text{
    align-self: center;
    font-weight: 400;
    flex-basis: 400px;
}

.newsletter__img img{
    max-width: 100%;
}

.newsletter-input{
    position: relative;
    margin-top: 15px;
    display: inline-block;
}

.newsletter-input input{
    background-color: transparent;
    border: #333333 solid 1px;
    padding: 0.5rem;
    outline: none;
}


.newsletter form{
    margin-top: 1.3rem;
    position: relative;
}
.newsletter__arrow{
    position: absolute;
    right: 5px;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}

@media (max-width:945px) {
    .newsletter__text{
        flex-basis: 500px;
        text-align: center;
        margin-top: 20px;
    }
    .newsletter form{
        margin-top: 0rem;

    }
}
/* ######### start: footer ########### */
footer{
    display: flex;
    justify-content: space-between;
    padding-bottom: 5rem;
}
.footer__list{
    display: flex;
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

@media  (max-width:485px) {
    .footer__list{
        flex-direction: column;
    }
}
/* ######### start: footer ########### */
.details{
    /*
    display: grid;
    grid-template-columns: repeat(8,1fr);
    */
    display: flex;
    justify-content: space-between;
    grid-gap: 30px;
    gap: 30px;
    flex-wrap: wrap;
}

.det-imgs{
    display: flex;
}

.details__images{
    align-self: top;
    padding-top: 10%;
}

.det-sml{
    -webkit-transform: scale(0.55);
            transform: scale(0.55);
    width: 100%;
    margin-bottom: -70px;
}
.details__main-img{
    /*
    grid-column: 2/5;
    align-self: center;
    */
    flex-basis: 400px;
    align-self: center;
}

.details__main-img img{
    width: 100%;
}
.det-big{
    /* width: 80%; */
    width: 400px;
}
.details__text{
    /*
    grid-column: 5/9;
    align-self: center;
    */
    flex: 1 1;
}
.details__price{
    font-size: 2.375rem;
    font-weight: 500;
    margin-bottom: 1rem;
}
.details__name{
    font-weight: 400;
    font-size: 2.375rem;
}
.details__description{
    /* padding-right: 10rem; */
    line-height: 1.8;
    font-weight: 300;
    color: #606060;
    margin: 0.5rem 0 1rem;
}


    

.lft{
    object-fit:none;
    object-position: -120px -10px ;
    width: 100%;
    -webkit-transform: scale(0.55);
            transform: scale(0.55);
}
.rgt{
    object-fit:none;
    object-position: -5px -150px ;
    width: 100%;
    -webkit-transform: scale(0.55);
            transform: scale(0.55);
}
.details__colors{
    margin-bottom: 2rem;
    display: flex;
    width: 50%;
    justify-content: space-between;
    align-items: center;
}
.colors{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}
.color{
    width: 2.5rem;
    height: 2.5rem;
    display: inline-block;
    border: none;
    cursor: pointer;
}
.color-choice{
    text-transform: capitalize;
}
.color:nth-child(1){
    background-color:#D07071 ;
}
.color:nth-child(2){
    background-color: #7074D0;
}
.color:nth-child(3){
    background-color: #333333;
}
.active{
    position: relative;
}
.active::before{
    content: "";
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    background-color: white;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}

.details__cart{
    display: flex;
    grid-gap: 2rem;
    gap: 2rem;
}
.details__cart span{
    border: solid 1px #333333;
    border: solid 1px var(--main-balck);
    display: flex;
    align-items: center;
    padding: 1rem 1.5rem;
}

@media (max-width:900px){
    .details{
        flex-direction:column;
    }
    .details__main-img{
        width: 90%;
    }
    .details__colors{
        display: flex;
        flex-direction: column;
        align-items:center;
        text-align: left;
        grid-gap: 10px;
        gap: 10px;
    }
    .color-choice{
        display: block;
    }
}
/* ################ CART PAGE ################# */
.cart{
    margin: 10rem 10rem 0 0;
}
.cart__title{
    display: block;
    border-bottom: solid #E6E6E6 1px;
    padding-bottom: 1rem;
}
.cart__item{
    font-weight:300;
    padding: 3rem 0;
    
    display: grid;
    grid-template-columns: repeat(13,1fr);
    align-items: center;
    justify-items: left;
    border-bottom: solid #E6E6E6 1px;
}
.item__img{
    width: 4rem;
    grid-column: span 1;
}
.item__name{
    grid-column: span 4;
}
.item__price, .item__qnt{
    grid-column: span 2;
}
.cart__item .btn{
    grid-column: span 3;
    display: inline;
    min-width: 0;
}

.delete-order{
    font-weight: 700;
    background: none;
    border: none;
    cursor: pointer;
    margin-left: 2rem;
    padding: 1rem;
}

.btn-cart{
    width: 11.8rem;
    text-align: center;
}


/* ########## CART TOTAL ############" */
.cart__total{
    display: grid;
    grid-template-columns: repeat(13,1fr);
    align-items: center;
    background-color: #F6F6F6;
    justify-items:left;
    padding: 3rem 0;
    margin-bottom: 30px;
}
.total{
    padding-left: 2rem;
}
.cart__total .total{
    grid-column: 1/6;
}
.total__qnt,.total__price{
    grid-column: span 2 ;
}
.cart__total .btn{
    grid-column: span 3;
} 


